<template>
  <div class="document-container">
    <h6 class="document-title">
      {{ questionnaire.name }}
    </h6>

    <div class="" v-if="questionnaire.type === 'only_file'">
      <div class="document-clue" v-if="questionnaire.first_help_text">
        {{ questionnaire.first_help_text }}
      </div>
      <div class="achievement-list__item--info--block">
        <file-component
          :acceptFormats="questionnaire.acceptFormats"
          v-if="!only_file_document"
          @change="file => setFiles(file)"
        ></file-component>
        <div class="file-container" v-else>
          <div class="file-container__files questionnaire-file">
            <a
              :href="getDocumentLink(only_file_document)"
              target="_blank"
              class="file-container__files--file"
            >
              <v-icon name="file"></v-icon>
              <span>{{ addDocumentName(only_file_document) }}</span>
            </a>
            <v-icon
              class="times-icon"
              @click.stop="only_file_document = null"
              name="times"
            ></v-icon>
          </div>
        </div>
      </div>
    </div>

    <div class="" v-if="questionnaire.type === 'multiple'">
      <div class="document-clue" v-if="questionnaire.first_help_text">
        {{ questionnaire.first_help_text }}
      </div>
      <upload-questionnaire-characteristic
        :characteristics="characteristics"
        @remove="removeCharacteristic"
        @add="addCharacteristic"
      ></upload-questionnaire-characteristic>
    </div>

    <div class="" v-if="questionnaire.type === 'radio_and_multiple'">
      <div class="document-clue">
        {{ questionnaire.subname }}
      </div>
      <FormulateInput
        class="inline-checkbox"
        type="radio"
        :name="`yes-${questionnaire.id}`"
        v-model="yesValue"
        :options="yesno"
      ></FormulateInput>
      <div v-if="yesValue === 'yes'" class="achievement-spec-block--reports">
        <div class="document-clue" v-if="questionnaire.first_help_text">
          {{ questionnaire.first_help_text }}
        </div>
        <upload-questionnaire-report
          :reports="reports"
          @removeReport="removeReport"
          @addReport="addReport"
        ></upload-questionnaire-report>
        <div class="document-clue" v-if="questionnaire.second_help_text">
          {{ questionnaire.second_help_text }}
        </div>
        <file-component multiple @change="file => setFiles(file)">
        </file-component>
      </div>
    </div>
  </div>
</template>

<script>
import FileComponent from '@/components/Ui/file'
import UploadQuestionnaireReport from './UploadQuestionnaireReport.vue'
import UploadQuestionnaireCharacteristic from './UploadQuestionnaireCharacteristic.vue'
const { VUE_APP_API_MEDIA } = process.env
export default {
  components: {
    FileComponent,
    UploadQuestionnaireReport,
    UploadQuestionnaireCharacteristic,
  },
  props: {
    questionnaire: {
      type: Object,
      required: true,
    },
    isProfile: {
      type: Boolean,
      required: false,
    },
  },
  data() {
    return {
      id: this.questionnaire.id,
      media: VUE_APP_API_MEDIA,
      form: {},
      yesValue: 'yes',
      yesno: {
        yes: 'Да',
        no: 'Нет',
      },
      reports: [],
      documents: [],
      characteristics: [],
      only_file_document: null,
    }
  },
  mounted() {
    if (
      this.questionnaire.type === 'only_file' &&
      this.questionnaire.documents
    ) {
      this.only_file_document = this.questionnaire.documents
    }
    if (this.questionnaire.type === 'multiple' && this.questionnaire.data) {
      this.characteristics = this.questionnaire.data
    }
    if (
      this.questionnaire.type === 'radio_and_multiple' &&
      this.questionnaire.data
    ) {
      this.reports = this.questionnaire.data.reports
    }
  },
  methods: {
    addDocumentName(document) {
      if (document) {
        const split = document.split('/')
        return split[split.length - 1]
      }
    },
    getDocumentLink(path) {
      return `${this.media}/${path}`
    },
    setFiles(file) {
      this.documents = file
      console.log(this.documents)
    },
    addReport(reportForm) {
      this.reports.push(reportForm)
    },
    async removeReport(key, id) {
      if (id) {
        try {
          await this.$store.dispatch('app/deleteItem', {
            entityName: 'report',
            id,
            nothingStoreDelete: true,
          })
        } catch (e) {
          console.log(e)
          this.$errors.show(e.response.data)
        }
      }
      this.reports.splice(key, 1)
    },
    async removeCharacteristic(key, id) {
      if (id) {
        try {
          await this.$store.dispatch('app/deleteItem', {
            entityName: 'characteristic',
            id,
            nothingStoreDelete: true,
          })
        } catch (e) {
          console.log(e)
          this.$errors.show(e.response.data)
        }
      }
      this.characteristics.splice(key, 1)
    },
    addCharacteristic(form) {
      this.characteristics.push(form)
      console.log(this.characteristics)
    },
  },
}
</script>

