<template>
  <div class="achievement-spec-block characteristic">
    <li
      class="achievement-list__item"
      v-for="(item, key) in characteristics"
      :key="key"
    >
      <div class="achievement-list__item--read">
        <div class="achievement-list__item--tools">
          <span class="count">{{ key + 1 }}</span>
          <a-tooltip placement="top">
            <template slot="title">
              <span>Удалить характеристику</span>
            </template>
            <v-icon
              @click="deleteCharacteristic(key, item.id)"
              class="remove"
              name="times"
            ></v-icon>
          </a-tooltip>
        </div>
        <div class="achievement-list__item--read--item item-name">
          <span>{{ item.document_type }}</span>
        </div>
        <div class="achievement-list__item--read--item">
          <span>{{ item.year_manufacture }}</span>
        </div>
        <div class="achievement-list__item--read--item">
          <span>{{ item.who_issued }}</span>
        </div>
        <div
          class="file-container"
          v-if="item.documents && item.documents.length"
        >
          <div class="file-container__files">
            <a
            v-if="item.documents[0].document"
              :href="getDocumentLink(item.documents[0].document)"
              target="_blank"
              class="file-container__files--file"
            >
              <v-icon name="file"></v-icon>
              <span>{{
                item.documents[0].id
                  ? addDocumentName(item.documents[0].document)
                  : item.documents[0].name
              }}</span>
            </a>
            <a
            v-else
              class="file-container__files--file"
            >
              <v-icon name="file"></v-icon>
              <span>{{
                item.documents[0].id
                  ? addDocumentName(item.documents[0].document)
                  : item.documents[0].name
              }}</span>
            </a>
          </div>
        </div>
      </div>
    </li>
    <li class="achievement-list__item" v-if="isVisible && !loading">
      <div class="achievement-list__item--info">
        <div
          class="achievement-list__item--info--block achievement-form-multiple"
        >
          <x-form
            class="achievement-spec-block--form"
            v-model="form"
            :valuses="form"
            @submit="add"
            name="form"
          >
            <div class="achievement-spec-block--form-item characteristic">
              <span class="count">{{ characteristics.length + 1 }}</span>
              <x-input
                name="document_type"
                type="text"
                validation="required"
                label="Вид документа"
                class="item-name"
                error-behavior="submit"
              ></x-input>
              <x-input
                name="year_manufacture"
                type="text"
                validation="required"
                label="Год выдачи"
                class="item-name item-name-year"
                error-behavior="submit"
              ></x-input>
              <x-input
                name="who_issued"
                type="select"
                validation="required"
                :options="issuingAuthority"
                label="Кто выдал"
                class="item-participation"
                error-behavior="submit"
              >
              </x-input>
            </div>

            <div class="achievement-spec-block--form-item">
              <file-component @change="file => setFiles(file)">
              </file-component>
            </div>
            <a-button
              type="info"
              block
              html-type="submit"
              class="document-container-button"
            >
              <v-icon name="plus"></v-icon>
              <span>Добавить</span>
            </a-button>
          </x-form>
        </div>
      </div>
    </li>
  </div>
</template>

<script>
import FileComponent from '@/components/Ui/file'
const { VUE_APP_API_MEDIA } = process.env
import { issuingAuthority } from '@/helpers/select-data'
export default {
  components: {
    FileComponent,
  },
  props: {
    characteristics: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      loading: false,
      form: {},
      media: VUE_APP_API_MEDIA,
      issuingAuthority: issuingAuthority,
    }
  },
  computed: {
    isVisible() {
      return this.characteristics.length < 3
    },
  },
  methods: {
    add() {
      this.loading = true
      this.$emit('add', { ...this.form })
      setTimeout(() => {
        this.form = {}
        this.loading = false
      }, 0)
    },
    deleteCharacteristic(key, id) {
      this.$emit('remove', key, id)
    },
    getDocumentLink(path) {
      return `${this.media}/${path}`
    },
    addDocumentName(document) {
      if (document) {
        console.log(document)
        const split = document.split('/')
        return split[split.length - 1]
      }
    },
    setFiles(files) {
      this.form.documents = files
    },
  },
}
</script>

