import Upload from '@/components/Student/UploadQuestionnaire.vue'
import cloneDeep from 'lodash.clonedeep'
import { ACHIEVEMENT } from '@/helpers/constants'
import { issuingAuthority, documentView } from '@/helpers/select-data'

export default {
  components: { Upload },
  data() {
    return {
      loading: true,
      buttonDisabled: false,
      questionnaries: [],
      participantQuestionnaire: {
        // report_card: [], массив табелей(2шт)
        //characteristic: [] массив файлов
      },
      isProfile: false,
      issuingAuthority: issuingAuthority,
      documentView: documentView,
    }
  },
  computed: {
    userInfo() {
      return this.$store.state.user.userInfo
    },
    initialIndicators() {
      return this.$store.state.app.indicator
    },
  },
  async beforeRouteLeave (to, from, next) {
    console.log(to.query)
    if (to.query.next) {
      next()
      return
    } else {
      if (await this.protectRouting()) next()
    }
  },
  async mounted() {
    this.getBaseIndicators()
    const indicators = await this.getIndicatorsReport()
    this.questionnaries = [...this.questionnaries, ...indicators]
    
    this.isProfile = this.getType()
    if (this.isProfile) {
      this.participantQuestionnaireResponse = await this.getParticipantQuestionnaire()
      this.renderQuestionnaires(this.participantQuestionnaireResponse)
    }
    this.loading = false
  },
  methods: {
    async getParticipantQuestionnaire() {
      try {
        const response = await this.$store.dispatch('app/getEntities', {
          entityName: 'participant-questionnaire'
        })
        return response.data
      } catch (e) {
        console.log(e)
      }
    },
    async renderQuestionnaires(response) {
      this.questionnaries[this.questionnaries.findIndex(q => q.id === 'autobiography')].documents = response.autobiography
      this.questionnaries[this.questionnaries.findIndex(q => q.id === 'certificate_place_study')].documents = response.certificate_place_study
      this.questionnaries[this.questionnaries.findIndex(q => q.id === 'characteristic')].data = response.characteristic
      if (response.report_card) {
        response.report_card.map(item => {
          this.questionnaries[this.questionnaries.findIndex(q => q.id === item.indicator_report_card)].data = item
        })
      }
      console.log(this.questionnaries)
    },
    getType() {
      return this.$route.path.includes('profile')
    },
    getBaseIndicators() {
      this.questionnaries = [
        {
          id: 'certificate_place_study',
          name: 'Справка с места учебы*',
          first_help_text: 'Прикрепите сканированную копию справки с места учебы в формате jpeg, pdf, имеющую печать образовательной организации, датированную не ранее 05.04.2021 г. Можно прикрепить потом в личном кабинете. Отправить заявку без этого нельзя',
          type: 'only_file',
          acceptFormats: [
            'image/jpeg',
            'application/pdf',
          ]
        },
        {
          id: 'autobiography',
          name: 'Автобиография конкурсанта*',
          first_help_text: 'Можно прикрепить только 1 документ формата doc, docx',
          type: 'only_file',
          acceptFormats: [
            'application/msword',
            'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
            'application/vnd.openxmlformats-officedocument.wordprocessingml.template',
            'application/vnd.ms-word.document.macroEnabled.12',
            'application/vnd.ms-word.template.macroEnabled.12',
          ]
        },
        {
          id: 'characteristic',
          name: 'Характеристики, отзывы и ходатайства',
          first_help_text: 'Вы можете прикрепить документы, подтверждающие Ваше участие и вклад в выбранном Вами направлении за последние два года. Например, характеристики, ходатайства и т.д. студенческих организаций (объединений), администрации вуза/ссуза и т.п. За каждый отдельный новый документ Вы можете получить 1 балл. Максимальное количество баллов – 3',
          type: 'multiple',
        },
      ]
    },
    async getIndicatorsReport() {
      try {
        const response = await this.$store.dispatch('app/getEntities', {
          entityName: 'indicator-report-card'
        })
        return this.renderIndicators(response)
      } catch (e) {
        console.log(e)
      }
    },
    async renderIndicators(indicators) {
      return indicators.data.map(indicator => {
        indicator.type = 'radio_and_multiple'
        return indicator
      })
    },
    getFormData(form) {
      const formData = new FormData()
      for (let i in form) {
        if (i === 'documents') {
          form[i].map(file => {
            formData.append('documents', file)
          })
        } else {
          formData.append(i, form[i])
        }
      }
      return formData
    },
    addDocumentName(document) {
      if (document) {
        const split = document.split('/')
        return split[split.length-1]
      }
    },
    renderDocuments(documents) {
      return documents.map(document => {
        document.documentName = this.addDocumentName(document.document)
        return document
      })
    },
    trimWhitespaces(object) {
      for (let i in object) {
        if (object[i] === String) object[i].trim()
      }
    },
    async protectRouting() {
      const { isConfirmed } = await this.$swal.fire({
        title: 'Есть несохраненные данные',
        text: 'Вы точно хотите уйти со страницы? Несохраненные данные будут утеряны.',
        showCancelButton: true,
        confirmButtonText: 'Продолжить',
        cancelButtonText: 'Отмена',
      })
      return isConfirmed
    },
    async onApplicate(type) {
      try {
        let autobiography
        let certificate_place_study
        let reportCards = []
        let characteristic = []
        const questionnaries = []
        let mistakes = []

        let isPresents = []

        this.$refs.questionnaries.map(questionnaire => {
          if (questionnaire.only_file_document) {
            isPresents.push(questionnaire.id)
          }
          questionnaries.push({
            reports: [...questionnaire.reports],
            documents: [...questionnaire.documents],
            id: questionnaire.id,
            type: questionnaire.questionnaire.type,
            characteristics: [...questionnaire.characteristics]
          })
        })
        await Promise.all(questionnaries.map(async questionnaire => {
          if (questionnaire.id === 'autobiography') {
            if (!questionnaire.documents.length) {
              if (isPresents.includes(questionnaire.id)) return
              mistakes.push('Прикрепите автобиографию')
              Promise.reject('error')
            } else {
              autobiography = questionnaire.documents[0]
            }
          }
          if (questionnaire.id === 'certificate_place_study') {
            if (!questionnaire.documents.length) {
              if (isPresents.includes(questionnaire.id)) return
              mistakes.push('Прикрепите справку об обучении')
              Promise.reject('error')
            } else {
              certificate_place_study = questionnaire.documents[0]
            }
          }
          if (questionnaire.id === 'characteristic' && !mistakes.length) {
            try {
              const newCharacteristics = questionnaire.characteristics.filter(c => !c.id)
              console.log(newCharacteristics)
              const response = await this.createCharacteristics(newCharacteristics)
              if (!response.length) return
              characteristic.push(response)
            } catch(e) {
              console.log(e)
            }
          }
          if (questionnaire.type === 'radio_and_multiple' && !mistakes.length) {
            try {
              const response = await this.createReportCard(questionnaire)
              if (!response) return
              reportCards.push(response)
            } catch(e) {
              console.log(e)
            }
          }
          return ''
        }))
        if (mistakes.length) {
          mistakes.map(mistake => {
            this.$toast.error(mistake)
          })
          mistakes = []
          return
        }
        let data = cloneDeep({
          report_card: reportCards,
          autobiography,
          certificate_place_study,
          characteristic
        })
        if (!data.characteristic.length) delete data.characteristic
        if (isPresents.includes('autobiography')) delete data.autobiography
        if (isPresents.includes('certificate_place_study')) delete data.certificate_place_study
        data = this.getFormData(data)
        console.log(data)
        // return
        this.buttonDisabled = true
        await this.$store.dispatch('app/saveEntity', {
          id: this.participantQuestionnaireResponse?.id,
          data,
          method: this.isProfile ? 'patch' : 'post',
          entityName: 'participant-questionnaire',
        })
        this.$toast.success('Успешно сохранено')
        this.$router.push({path: `/${type}`, query: { next: true }})
      } catch (e) {
        console.log(e)
        this.$errors.show(e.response.data)
      } finally {
        this.buttonDisabled = false
      }
    },
    async createReportCard(reportCard) {
      console.log(reportCard)
      if (!reportCard.reports.length) return
      // const newReports = reportCard.reports.filter(c => !c.id)
      const reports = await this.createReports(reportCard.reports)
      const data = this.getFormData({
        documents: reportCard.documents,
        reports,
        indicator_report_card: reportCard.id
      })
      console.log(data)
      const response = await this.$store.dispatch('app/saveEntity', {
        entityName: 'report-card',
        method: 'post',
        data
      })
      return response.id
    },
    async createReports(reports) {
      return await Promise.all(reports.map(report => {
        return new Promise(async res => {
          const response = await this.$store.dispatch('app/saveEntity', {
            entityName: 'report',
            method: 'post',
            data: report
          })
          res(response.id)
        })
      }))
    },
    async createCharacteristics(characteristics) {
      return await Promise.all(characteristics.map(characteristic => {
        return new Promise(async res => {
          const data = this.getFormData(cloneDeep(characteristic))
          const response = await this.$store.dispatch('app/saveEntity', {
            entityName: 'characteristic',
            method: 'post',
            data
          })
          res(response.id)
        })
      }))
    },
  },
}
